.footer {
    width: 100%;
    background-color: $color-grey;
    color: $color-white;
    text-align: center;

    @media (min-width:768px) {
        padding: 2rem;
    }

    @media (min-width:1280px) {
        padding: 4rem;

    }
    
    &--buttom {
        background-color: $color-white;
        padding: 1rem 0.9rem 0.5rem;
        border-radius: 50%;
        text-decoration: none;
        color: $color-orange ;
        font-size: 1rem;
        position: relative;
        top: -0.5rem;

        @media (min-width:768px) {
            top:-2.5rem;
        }

        @media (min-width:1280px) {
            top: -4.5rem;
        }

        &_arrow {
            width: 20px;
            height: 20px;
        }
               
    }

    &--navegator {

        @media (min-width:768px) {
            display: flex;
            justify-content:center;
            align-items: center;
        }


        &_list {

            @media (min-width:768px) {
                padding: 0 5rem 0 5rem;
                text-align: start;
                order: 1;                
            }
        

            &_section {
                font-family: $font-roboto;
                font-size: 0.8rem;
                margin: 1rem 0 1rem 0;
                font-weight: normal;
                font-stretch: normal;
                font-style: normal;
                line-height: 1.88;
                letter-spacing: normal;
                text-decoration: none;


                &_item {
                    color: $color-white;
                    text-decoration: none;
                        
                }
                
                @media (min-width:768px) {
                    line-height: 2.5;
                    font-size: 1rem;
                }
            }    
        }
            
        &_list_copy {
            font-family: $font-roboto;
            padding-bottom: 2rem;
            font-size: 0.8rem;
            font-weight: normal;
            font-stretch: normal;
            font-style: normal;
            line-height: 1.88;
            letter-spacing: normal;

            @media (min-width:768px) {
                font-size: 1rem;
                padding: 0 2rem 0 2em;
                order: 0;
            }

        }

         
    }
}



